import Vue from "vue";
import Events from "../Events.js";
import DownloadIcon from "../../../common/assets/download.svg";
import PlayIcon from "../../../common/assets/play.svg";
import SettingsIcon from "../../../common/assets/sliders.svg";


export default new Vue({
        el: '#menu',
        template: `
            <menu id="menu">
            <button class="btn btn-action btn-primary circle btn-lg play" v-html="PlayIcon" @click="play"></button>
            <button class="btn btn-action btn-primary circle btn-lg download" v-html="DownloadIcon" @click="exportStory"></button>
            <button class="btn btn-action btn-primary circle btn-lg settings" v-html="SettingsIcon" @click="settings"></button>
            </menu>
        `,
        data: { PlayIcon, DownloadIcon , SettingsIcon , eventTarget : null},
        methods : {
            sendEvent(eventType) {
                if(this.eventTarget){
                    this.eventTarget.dispatchEvent(new CustomEvent(eventType));
                }
            },
            play() {
                this.sendEvent(Events.CHATTER_PLAY);
            },
            exportStory(){
                this.sendEvent(Events.CHATTER_EXPORT);
            },
            settings(){
                this.sendEvent(Events.CHATTER_SETTINGS);
            }
        }
    });






